import { useState } from "react";
import axios from "axios";
import { Document, Page, pdfjs } from 'react-pdf';

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

type QA = {
  question: string;
  answer: string;
};

export default function Home() {
  const [file, setFile] = useState<File | null>(null);
  const [userPrompt, setUserPrompt] = useState<string>("");
  const [summary, setSummary] = useState<string | null>(null);
  const [questions, setQuestions] = useState<QA[]>([]);
  const [loading, setLoading] = useState(false);
  const [pdfUrl, setPdfUrl] = useState<string | null>(null);


  const handleUploadAndAnalyze = async () => {
    if (!file || !userPrompt) return;
    setLoading(true);
    
    setSummary(null);
    setQuestions([]);

    try {
      const formData = new FormData();
      formData.append("file", file);

      const uploadRes = await axios.post("http://localhost:8000/upload", formData);
      const textContent = uploadRes.data.content;

      const analysisRes = await axios.post("http://localhost:8000/analyze", {
        text: textContent,
        user_prompt: userPrompt,
      });
      console.log("üîç Backend response:", analysisRes.data);
      setSummary(analysisRes.data.summary || null);
      setQuestions(analysisRes.data.questions || []);
    } catch (error) {
      console.error("Error during upload/analyze:", error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="p-4 max-w-2xl mx-auto">
      <h1 className="text-2xl font-bold mb-4">Smart PDF AI Assistant</h1>

      <input
        type="file"
        accept="application/pdf"
        onChange={(e) => {
          const selectedFile = e.target.files?.[0] || null;
          setFile(selectedFile);
          if(selectedFile){
            setPdfUrl(URL.createObjectURL(selectedFile))
          }
        }}
        className="mb-4"
      />

      <input
        type="text"
        placeholder="What would you like to do with the PDF?"
        className="border p-2 w-full mb-4"
        value={userPrompt}
        onChange={(e) => setUserPrompt(e.target.value)}
      />

      <button
        onClick={handleUploadAndAnalyze}
        className="bg-blue-600 text-white px-4 py-2 rounded"
      >
        Analyze
      </button>

      {loading && <p className="mt-4 text-gray-600">Processing PDF with AI...</p>}

      {summary && (
        <div className="mt-6">
          <h2 className="text-lg font-semibold mb-2">Summary:</h2>
          <p>{summary}</p>
        </div>
      )}

      {questions.length > 0 && (
        <div className="mt-6">
          <h2 className="text-lg font-semibold mb-2">Flash Questions:</h2>
          <ul className="list-disc ml-6 space-y-3">
            {questions.map((q, idx) => (
              <li key={idx}>
                <strong>Q:</strong> {q.question}
                <br />
                <strong>A:</strong> {q.answer}
              </li>
            ))}
          </ul>
        </div>
      )}
    </main>
  );
}
